import '../css/nav.scss';
import logo from '../imgs/logo_gumbo_mambo_-_logo_website_-_final.png'
import crap from '../imgs/Logo Gumbo Mambo - Mascot.png'


export function nav() {
    const menuArray = ["Home", "MENU/ORDER", "RESERVATION", "MORE"]
    return (
        <div className="navContainer">
            {/*         
        <div id="navBar">
            <img src={logo} alt="Logo" className="navLogo"/>
            <li ><a>Home</a> <i className="fas fa-caret-down"></i> </li>
            <li><a>Menu / Order now</a> <i className="fas fa-caret-down"></i></li>
            <li><a>Reservation</a> <i className="fas fa-caret-down"></i></li>
            <li><a>More</a> <i className="fas fa-caret-down"></i></li>
        </div>
         */}
            <nav className="nav">
                <div className="nav_section">
                    {menuArray.map((item) => {
                        return <div className="nav_item navButton">
                                    <a href="" className="button" >
                                        <span className="button_line button_line-top"></span>
                                        <span className="button_line button_line-right"></span>
                                        <span className="button_line button_line-bottom"></span>
                                        <span className="button_line button_line-left"></span>
                                        {item}
                                     </a>
                                </div>
                    })}
                     
                </div>
               
            </nav>
            <i className="fas fa-shopping-cart"></i>
            <img src={logo} alt="" className="navLogo" />
        </div>
    );
};

export default nav;