import '../css/modalButton.scss'
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import { useState } from 'react';

function ChefModal(props) {
  return (
    <Modal
      {...props}
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header closeButton>
        <Modal.Title id="contained-modal-title-vcenter">
          {props.button.title}
        </Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <h4>{props.button.img}</h4>
        <p>
          {props.button.desc}
        </p>
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={props.onHide}>Close</Button>
      </Modal.Footer>
    </Modal>
  );
}


export function ModalButton() {
  const [chefModalShow, setChefModalShow] = useState(false);
  const [locationModalShow, setLocationModalShow] = useState(false);
  const [GallerieModalShow, setGallerieModalShow] = useState(false);
  const [activeButton, setActiveButton] = useState({});
  return <div className="buttonContainer">
    <Button variant="danger" onClick={() => {
      setActiveButton("Chef")
      setChefModalShow(true)
    }}>
      Our Chef
    </Button>
    <Button variant="danger" onClick={() => {
      setActiveButton("Location")
      setLocationModalShow(true)
    }}>
      Truck location
    </Button>
    <Button variant="danger" onClick={() => {
      setActiveButton("Gallerie")
      setGallerieModalShow(true)
    }}>
      Reviews
    </Button>

    {/* {if(activeButton=="Chef"){
      console.log(hi)
    }} */}
    <ChefModal
      button={activeButton}
      onHide={() =>console.log("hi")}
    />

  </div>
}

export default ModalButton